import houses from "./houses.json";
import House from "../components/House";
const Index = () => (
  <div>
    <h1>Houses to stay</h1>
    <div className="houses">
      {houses.map((house, idx) => {
        return <House key={idx} {...house} />;
      })}
    </div>
  </div>
);

export default Index;
